:root {
  --primary-color: #144f68;
}

* {
  font-family: "Open Sans", sans-serif;
  box-sizing: border-box;
}

.nav {
  background: var(--primary-color);
}

.weather-card {
  max-width: 10rem;
  background: yellow;
  border-radius: 5px;
  background: teal;
  height: 100px;
  display: flex;
  padding: 0 5px;
  align-items: flex-end;
}

.weather-temp {
  top: 0;
  right: 5px;
}

.search-wrapper {
  margin-top: 1rem;
  margin-bottom: 1rem;
}

.search {
  background: tomato;
  padding: 2rem;
  border-radius: 5px;
}

.search .field input {
  border: none;
  border-radius: 5px;
  padding: 15px 10px;
  transition: box-shadow 0.3s;
}

.search .field input:focus {
  box-shadow: 0 8px 17px 2px rgba(0, 0, 0, 0.14),
    0 3px 14px 2px rgba(0, 0, 0, 0.12), 0 5px 5px -3px rgba(0, 0, 0, 0.2);
}

.search-button {
  right: 0;
  top: 0;
  border-radius: 4px;
  border: none;
  background: #144f68;
  color: #fff;
}

.markup {
  color: tomato;
}

.footer {
  text-align: right;
}
